@media (min-width: 768px) {
  .main-nav--opened .main-nav__wrapper {
    display: grid;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    padding: 0;
    background-color: transparent;
    min-height: 151px;
    margin: 0 40px 0 40px;
