  .main-nav__wrapper:focus {
    display: flex;
  }
  .main-nav__list.site-list {
    display: flex;
    grid-row: 2;
    margin: 0;
    padding: 0;
  }
  .main-nav__list.user-list {
    display: flex;
    grid-row: 1;
    margin: 0;
    grid-column: 2;
    padding: 0;
    justify-content: end;
  }
  .main-nav__list-item {
    padding: 10px;
    margin: 0;
  }
  .main-nav__list-item-search {
    visibility: hidden;
    width: 0;
  }
  .main-nav__list-item-search svg {
    visibility: visible;
  }
  .main-nav__list-item-invisible {
    width: 0;
    height: 0;
  }
  .main-nav__list-item-visible {
    font-size: 17px;
    margin-left: 66px;
    position: relative;
  }
  .main-nav__list-item-visible::before {
    content: "";
    position: absolute;
    top: -16px;
    left: -153px;
    height: 82px;
    width: 1px;
    background-color: rgba(0, 0, 0, 0.1);
